.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



/*Body*/
body {
  font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
  padding-top: 85px;
  background-color: #dfd3c3;
  color: #dfd3c3;
}



/*Navigation Bar*/
.navbar {
  background-color: #1b262c;
}
.navbar-symbol {
  width: 2rem;
  height: auto;
}
.navbar-name {
  font-size: 2.5rem;
  padding-left: 1rem;
  color: #dfd3c3 !important;
}
.nav-item {
  padding: 0 10px;
}
.navbar-dark .navbar-nav .nav-link {
  color: #dfd3c3;
  color: rgba(223,211,195,.5);
}
.navbar-dark .navbar-nav .nav-link:hover {
  color: #dfd3c3;
  color: rgba(223,211,195,1);
}
.nav-link {
  font-size: 1.3rem;
}
.profile-img {
  width: 6rem;
  max-height: 100%;
  max-width: 100%;
  border-radius: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  display: inline-block;
  vertical-align: middle;
}
.uk-flag{
  transition-duration: 0.5s;
  opacity: 0.5;
}
.it-flag{
  transition-duration: 0.5s;
  opacity: 1;
}



/*Summary*/
.summary {
  background-color: #dfd3c3;
  color:#1b262c;
  text-align: center;
  padding-top: 7rem;
}
.frame {
    position: relative;
}
.profile-icon {
  font-size: 1.5rem;
  color:#1b262c;
  padding-right: 0;
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
}
 .info-container {
   padding: 1.5rem 0;
 }
.info-text{
  color: #1b262c;
  text-align: left;
  align-self: center;
  padding-top: 0.3rem;
  padding-left: 0;
}
.notClickable {
  text-decoration: none;
  color: #1b262c;
}
.notClickable:hover {
  text-decoration: none;
  color: #1b262c;
  cursor: default;
}
.isClickable {
  text-decoration: none;
  color: #1b262c;
}
.isClickable:hover {
  text-decoration: none;
  color: #1b262c;
  color: rgba(27,38,44,.5);
}
.youtube-frame {
  width: 18rem;
  height: 18rem;
  max-height: 100%;
  max-width: 100%;
  border: 0;
  margin-left: 20rem;
  vertical-align:middle;
  text-align:center;
}
.summary-data {
  font-size: 1.3rem;
  padding: 5rem 15rem;
  line-height: 200%;
}



/*Skills*/
.title-section {
  font-size: 3rem;
  margin: 0 auto;
  padding-bottom: 3rem;
}
.skills-container {
  padding: 6rem 10rem;
  background-color: #475860;
}
.top-skill-img {
  width: 13rem;
  height: 13rem;
  margin-bottom: 2rem;
  border-radius: 100%;
  opacity: 0.3;
  filter: grayscale(70%);
  -moz-filter: grayscale(70%);
  -ms-filter: grayscale(70%);
  -o-filter: grayscale(70%);
  -webkit-filter: grayscale(70%); /* Chrome 19+, Safari 6+, Safari 6+ iOS */
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
  position: relative;
  display: inline-block;
  margin: auto;
}
.container {
  position: relative;
  text-align: center;
  color: #dfd3c3;
  padding: 2rem 0;
}
.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.5rem;
}
.skill {
  margin: auto;
  padding: 0 3rem;
}
.skill-img {
  height: 2rem;
  width: 2rem;
  margin-left: 0.5rem;
}
.skill-img-div {
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
}
.skill-star {
  font-size: 1.75rem;
  padding-left: 0.5rem;
}
.skill-star-dark {
  color: #1b262c;
  color: rgba(27,38,44,.5);
}
.tooltips {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
}
.tooltips .tooltiptext {
  visibility: hidden;
  width: 320px;
  background-color: #1b262c;
  color: #dfd3c3;
  font-size: 0.8rem;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
  opacity: 0;
  transition: opacity 0.3s;
}
.tooltips .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 60px;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #1b262c transparent transparent transparent;
}
.tooltips:hover .tooltiptext {
  padding:1rem;
  visibility: visible;
  opacity: 1;
}
.tooltips:hover {
color: #1b262c;
transition: 0.5s;
}



/*Work Experience*/
.work-experience-container {
  padding: 6rem 10rem;
  color: #1b262c;
}



/*Projects*/
.projects-container {
  padding: 6rem 10rem;
  background-color: #475860;
  text-align: center;
  font-size: 2rem;
  line-height: 1.5;
}
.picture-project {
  width: 22rem;
  height: 22rem;
  border-radius: 100%;
}
.carousel-item {
  height: 600px;
  background-color: #475860;
}
.description {
  padding: 2% 15%;
  color:#dfd3c3;
  font-size: 1rem;
}
.btn-project {
  text-decoration: none;
  padding: 2rem 0 1rem 0;
}
.carousel-indicators li {
  background-color: #dfd3c3;
}
.carousel-indicators .active {
  background-color: #dfd3c3;
}
.btn-outline-light {
    color: #dfd3c3;
    border-color: #dfd3c3;
    font-size: 1.3rem;
}
.btn-outline-light:hover {
    color: #1b262c;
    border-color: #475860;
    background-color: #dfd3c3;
}
.carousel-control-prev-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23dfd3c3' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E");
}
.carousel-control-next-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23dfd3c3' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E");
}




/*Education*/
.education-container {
  padding: 6rem 10rem;
  color: #1b262c;
}
.uni-img{
  width:100%;
  padding: 0.5rem 5rem;
}
.uni-course {
  font-size: 1.5rem;
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
}
.uni-type {
  font-size: 1rem;
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
  padding: 0.5rem 0 0.5rem 0.5rem;
}
.tooltips-education:hover {
  color: #475860;
}


/*Collapsable*/
.nav-tabs > li{
	display: inline-block;
	line-height: 20px;
	height: 100%;
	text-transform:uppercase;
}
.nav-tabs > li a{
	display:block;
	padding:15px;
	color: #017470;
}
.nav-tabs > li a:hover, .nav-tabs > li:hover{
	background:	#017470;
  color:#fff;
	text-decoration: none;
}
.nav-tabs > li .active{
	background:	#05B1A4;
	color:#fff;
	text-decoration: none;
}
.bs-example{
  margin: 20px;
  padding-top: 150px;
}
.accordion .fa{
  margin-right: 0.5rem;
}
.accordion .btn{
  color:#017470;
  font-weight: bold;
  font-size: 1.2rem;
}
.accordion .card-body{
  color:#05B1A4;
  font-weight: bold;
  font-size: 1rem;
}
.container{
  padding: 1.5rem 0;
}
.card-header {
    padding: .5rem;
}



/*Courses*/
.courses-container {
  padding: 8rem 15rem;
  background-color: #475860;
}
.course-img{
  height: 2rem;
  width: 2rem;
  margin: 0.4rem 1rem;
  border-radius: 100%;
}
.course-title{
  font-size: 1.5rem;
  vertical-align: middle;
  align-items: center;
  align-self: center;
  align-content: center;
}
.course-certification {
  font-size: 1.9rem;
  color: #dfd3c3;
}
.lightbox {
	display: none;
	position: fixed;
	z-index: 999;
	width: 100%;
	height: 100%;
	text-align: center;
	top: 0;
	left: 0;
  background: #1b262c;
	background: rgba(27,38,44,0.5);
}
.lightbox img {
	max-width: 90%;
	max-height: 70%;
	margin-top: 10%;
}
.lightbox:target {
	outline: none;
	display: block;
}
.row-course-certification{
  margin-left: 0;
}



/*Footer*/
footer {
  background-color: #1b262c;
  color:#dfd3c3;
  text-align: center;
  bottom: 0;
  padding: 3% 0;
}
footer p {
  color:#dfd3c3;
}
.footer-symbol {
  color:#dfd3c3;
  font-size: 1.5rem;
}
.link-decoration {
  text-decoration: none;
}
.footer-symbol:hover {
  opacity: 0.6;
}
